/* @import url('https://fonts.googleapis.com/css?family=Cookie&display=swap'); */
@import url('https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap');
body{
    font-family: 'Josefin Sans', sans-serif;;
    font-size: 18px;
    line-height: 1.5em;
    margin: 0;
    padding: 0;
}
.menu-items{
   display: flex;
   flex-direction: row;
   justify-content: flex-end;
   flex-wrap: wrap;
   height: 70px;
   margin-top: 20px;
   /* margin-bottom: 10px; */
}
.header-links{
    padding: 2px;
    padding-right: 20px;
    letter-spacing: 2px;
    font-family: 'Josefin Sans', Tahoma, sans-serif;
    color: #888;
    font-size: 30px;
    text-decoration: none;
    border-bottom: 3px solid #56a79a2c;
}
.header-links:hover{
    color: rgba(255, 99, 71, 0.726);
}

.description{
    background: rgba(250, 240, 230, 0.205);
}
.description p{
    width: 50%;
    position: relative;
    left: 25%;
    right: 0%;
    text-align: center;
    display: block;
    font-family: 'Cookie', cursive, sans-serif;
    font-size: 24px;
    color: rgba(rgba(60, 82, 82, 0.144));
    text-shadow: 5px, 5px, 30px rgba(0, 0, 0, .4);
}
.description button{
    font-family: 'Josefin', sans-serif;
    cursor: pointer;
    outline: none;
    width: 120px;
    padding: 7px;
    display: block;
    margin: 0 auto;
    background-color: tomato;
    border: none;
    outline: none;
    border-radius: 25px;
    color: #eee;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, .4);
}
.profile-img{
    width: 200px;
    height: 200px;
    border-radius: 100%;
    background: linear-gradient(rgba(110, 56, 56, 0.8), rgba(0, 0, 0, .6));
    background-color: transparent;
    display: block;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 50px;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, .4);
}

/* .projects{
   
} */
.projects h1{
    text-transform: uppercase;
    text-align: center;
    text-decoration: underline rgba(255, 175, 71, 0.274);
    color: #888;
}

.projects h4{
    text-align: center;
    font-size: 14px;
}

.projects img{
    display: block;
    width: 50%;
    margin: auto;
    padding: 10px;
    border: 2px solid #888;
}
.projects p{
    text-align: center;
}

.projects p>a{
    text-align: center;
    /* position: relative;
    left: 25%; */
    color: rgba(255, 99, 71, 0.836);
}

.projects a:hover{
    color: orange;
}

.contacts h1{
    text-align: center;
    text-transform: uppercase;
    font-size: 18px;
}

.contacts img{
    width: 5%;
    height: 10%;
    margin: 2%;
}

.contacts .contact-icons{
    width: 100%;
    margin: auto;
    position: relative;
    left: 30%;
}

.title-fade-out{
    opacity: 0;
    animation-name: fade-out;
    animation-duration: 2s;
}
.title-fade-in{
    opacity: 100;
    animation-name: fade-in;
    animation-duration: 2s;
}

@keyframes fade-out{
    from { opacity: 100}
    to { opacity: 0}
}

@keyframes fade-in{
    from { opacity: 0}
    to { opacity: 100}
}
